<template>
  <div class="admin" :class="{hidden:this.$store.getters.loadingStatus}">
    <button @click="logout" class="sign-out" :class="{hidden:this.$store.getters.loadingStatus}">&times;</button>
    <h1 class="title"> 
      <span class="title__admin" :class="{hidden:this.$store.getters.loadingStatus}">admin</span><br>
      <span class="title__name">skultes</span><br>
      <span class="title__gym">gym</span>
    </h1>
    <LoadingSpinner v-if="this.$store.getters.loadingStatus" />
    <div v-else>
      <AppointmentList v-for="(date, index) in this.availableDates" :appointments="todaysAppointments(index)" :date="date.date_short" :day="date.day_text" />
      <InputForm/>
      <Cart v-if="this.$store.getters.cart.length > 0" :payment="false"/>
    </div>
  </div>
</template>

<script>
import AppointmentList from '@/components/AppointmentList.vue'
import InputForm from '@/components/InputForm.vue'
import LoadingSpinner from '@/components/LoadingSpinner.vue'
import Cart from '@/components/Cart.vue'
import db from '../components/firebaseInit'
import { getAuth, signOut, onAuthStateChanged } from "firebase/auth";
import { collection, query, where, getDocs, orderBy } from 'firebase/firestore'

export default {
  name: 'HomeView',
  components: {
    AppointmentList, LoadingSpinner, InputForm, Cart
  }, 
  data(){
    return{
      auth: null,
      date: '',
      day: '',
      dayDict: {
          'Monday': 'Pirmdien',
          'Tuesday': 'Otrdien',
          'Wednesday': 'Trešdien',
          'Thursday': 'Ceturtdien',
          'Friday': 'Piektdien',
          'Saturday': 'Sestdien',
          'Sunday': 'Svētdien'
        }
    }
  },
  mounted(){
    this.auth = getAuth()
      onAuthStateChanged(this.auth, (user) => {
          if(!user) {
              this.$router.push('/login')
          }
    }); 
    console.log('bang')
    this.$store.dispatch('fetchAppointments')
  },
  methods:{
    todaysAppointments(date){
      const appointmentList = []
      for (const appointment of this.$store.getters.allAppointments) {
          if(this.availableDates[date].value == appointment.date){
            appointmentList.push(appointment)
          }
      }
      return appointmentList
    },
    logout(){
      this.$store.dispatch('setLoadingStatus', true)
      setTimeout(() => {
        signOut(this.auth)
      }, 1500);
      
    }
  },
  computed:{
    availableDates() {
      const today = new Date()
      const startOfWeek = new Date(today)
      const dayOfWeek = today.getDay()
      const daysToSubtract = dayOfWeek === 0 ? 6 : dayOfWeek - 1
      startOfWeek.setDate(today.getDate() - daysToSubtract)
      const next7Days = []

      for (let i = 0; i < 7; i++) {
        const currentDate = new Date(startOfWeek);
        currentDate.setDate(startOfWeek.getDate() + i);
        
        const dateKey = currentDate.toLocaleString('en-US', { weekday: 'long'});
        const dateValue = currentDate.toLocaleDateString('en-US', { year: 'numeric', month: '2-digit', day: '2-digit' });
        const [month, day, year] = dateValue.split('/');
        const dateVal = parseInt(year+month+day)
        const dateLong = year + '-' + month + '-' + day
        const europeanDateKey = `${day}.${month}`;

        next7Days.push({
          day_text: this.dayDict[dateKey],
          date_short: europeanDateKey,
          date: dateLong,
          value: dateVal 
        });
        
      }
      return next7Days
    },
  },
}
</script>

<style scoped lang="scss">
  @import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;200&display=swap');
  @import '../components/colors';
  @import url('https://fonts.googleapis.com/css2?family=Jost:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
  .admin{
    transition: padding-top 1s ease;
    padding-top: 0;

    &.hidden{
      padding-top: calc(35vh - 42px);
    }
  }

  .sign-out{
    position: absolute;
    top:0;
    right:0;
    z-index: 1;
    font-size: 38px;
    border: none;
    width: 50px;
    height: 50px;
    background-color: unset;
    color: $color2;
    cursor: pointer;
    transition: color 1s ease;

    &.hidden{
      color: $color3;;
    }
  }

  .title{
    font-family: 'Jost', sans-serif;
    font-weight: 700;
    margin-bottom: 70px;
    text-transform: uppercase;
    font-style: italic;
    margin-top: 0;
    height: 160px;
    

    &__name{
      font-size: 80px;
      line-height: 50px;
      margin: 0 auto;
      width: 400px;
      height: 200px;
    }

    &__gym{
      font-size: 80px;
      line-height: 40px;
    }

    &__admin{
      font-size: 30px;
      line-height: 20px;
      transition: color 1s ease;

      &.hidden{
        color: $color3;;
      }
      // align-self: flex-start;
    }
  }  
</style>
